<template>
  <div>
  <el-row>
    <el-button type="success">添加</el-button>
    <el-button type="danger">删除</el-button>
  </el-row>
  <div id="mao">
    <div class="item"><div style="text-align:center;border: 0"></div><img src="../../../static/image/a.png" width="280" alt=""></div>
    <div class="item"><div style="text-align:center;border: 0"></div><img src="../../../static/image/b.png" width="280" alt=""></div>
    <div class="item"><div style="text-align:center;border: 0"></div><img src="../../../static/image/c.png" width="280" alt=""></div>
    <div class="item"><div style="text-align:center;border: 0"></div><img src="../../../static/image/d.png" width="280" alt=""></div>
  </div>
  </div>
</template>
<style>
  #mao{
    display:flex;
    flex-direction:row;
    flex-wrap: wrap;
    justify-content:space-around;
    align-items:center;
  }
  #mao .item{
    border-color: #99a9bf;
    border: 1px ;
    margin-top: 10px;
    margin-left: 10px;
  }
  #mao .item :hover{
    background-color:lightskyblue;

  }
</style>
<script>
  import axios from 'axios'
  axios.defaults.withCredentials = true
  export default {
    methods: {
      open() {
        this.$alert('<strong>这是添加服务窗口</strong>', '修改服务', {
          dangerouslyUseHTMLString: true
        })
      },
      del() {
        this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
      }
    },
    data() {
      return {
        tableData: []
      }
    },
    mounted:function () {
      //多选框获取类别

      axios.post('http://120.79.185.173:8080/agriculture/backend/service/serviceInfoGet.do',{sourceId:1,flag:0}).then(res => {
        // console.log(res.data)
      }).catch(() => {
        console.log("error");
      });
    }
  }
</script>



